/* src/pages/cart.css */
/* Subtle card shadow + border smoothing */
.card-soft {
  box-shadow: 0 6px 20px rgba(0, 0, 0, .06);
  border: 1px solid rgba(17, 24, 39, .06); /* gray-900 at low alpha */
}

/* Nice focus for inputs on non-Tailwind focus paths */
.card-soft input[type="text"],
.card-soft input[type="email"],
.card-soft input[type="tel"] {
  transition: box-shadow .18s ease, border-color .18s ease;
}

.card-soft input:focus {
  border-color: #fb923c; /* orange-400 */
  box-shadow: 0 0 0 3px rgba(251, 146, 60, .35);
}

/* Radio labels feel clickable */
.card-soft label {
  transition: background-color .15s ease, border-color .15s ease;
}

/* Smooth hover for item rows */
.card-soft:hover {
  border-color: rgba(17, 24, 39, .12);
}

/* Slim, pretty scrollbars for long carts (WebKit & modern browsers) */
*::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}
*::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, .12);
  border-radius: 9999px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}

/* Firefox scrollbar */
* {
  scrollbar-width: thin;
  scrollbar-color: rgba(0,0,0,.18) transparent;
}
/* src/pages/cart.css */
:root {
  --brand-1:#f97316; --brand-2:#ef4444;
  --ink-900:#111827; --ink-700:#374151;
  --muted:#6b7280;   --border:rgba(17,24,39,.12);
  --ring:rgba(249,115,22,.25);
  --card:#fff; --card-alt:#f9fafb;
  --shadow:0 10px 24px rgba(0,0,0,.08);
}

/* page + title */
.cart-page {max-width:80rem;margin:auto;padding:2rem 1rem;}
.cart-title{font-weight:800;font-size:clamp(1.25rem,1.2rem + 1vw,1.75rem);color:var(--ink-900);}
.cart-title small{color:var(--muted);font-weight:600;}

/* grid */
.cart-grid{display:grid;gap:1.25rem;}
@media(min-width:1024px){.cart-grid{grid-template-columns:1fr 360px;align-items:start;}}

/* address */
.cart-address{background:var(--card-alt);border:1px solid var(--border);border-radius:.75rem;padding:1rem;}
.cart-label{display:block;font-size:.9rem;color:var(--ink-700);margin-bottom:.5rem;font-weight:600;}
.cart-input{width:100%;height:42px;border:1px solid var(--border);border-radius:.65rem;padding:0 .9rem;background:#fff;color:var(--ink-900);font-weight:500;outline:none;transition:border-color .15s,box-shadow .15s;}
.cart-input:focus{border-color:var(--brand-1);box-shadow:0 0 0 3px var(--ring);}

/* item */
.cart-item{display:flex;justify-content:space-between;gap:1rem;padding:1rem;border:1px solid var(--border);border-radius:.9rem;background:var(--card);transition:.2s;}
.cart-item:hover{background:#fff;transform:translateY(-1px);box-shadow:var(--shadow);}
.cart-item__left{display:flex;gap:1rem;align-items:center;}
.cart-thumb{width:6rem;height:6rem;object-fit:cover;border-radius:.75rem;background:#f3f4f6;border:1px solid var(--border);}
.cart-item__name{font-weight:700;color:var(--ink-900);}
.cart-item__price{color:var(--brand-1);font-weight:800;}
.cart-meta{display:flex;gap:.5rem;align-items:center;color:var(--muted);font-size:.85rem;}

/* qty */
.qty{display:inline-flex;align-items:center;border-radius:.65rem;overflow:hidden;background:#f3f4f6;border:1px solid var(--border);}
.qty__btn{width:42px;height:42px;display:grid;place-items:center;border:none;background:transparent;cursor:pointer;transition:background .15s;}
.qty__btn:hover{background:#e5e7eb;}
.qty__btn--inc{background:linear-gradient(90deg,var(--brand-1),var(--brand-2));color:#000000;}
.qty__btn--inc:hover{filter:brightness(1.05);}
.qty__val{min-width:48px;text-align:center;font-weight:700;}

/* line + remove */
.cart-line{text-align:right;min-width:5.5rem;}
.line-total{font-weight:800;color:var(--ink-900);}
.cart-remove{background:none;border:none;color:#dc2626;font-size:.9rem;cursor:pointer;}
.cart-remove:hover{color:#b91c1c;}

/* summary */
.summary{background:var(--card-alt);border:1px solid var(--border);border-radius:.9rem;padding:1.25rem;}
.summary h3{margin:0 0 1rem;font-weight:800;font-size:1.15rem;color:var(--ink-900);}
.summary-row{display:flex;justify-content:space-between;font-size:.95rem;margin:.35rem 0;}
.summary-row--discount{color:#16a34a;}
.summary-total{display:flex;justify-content:space-between;font-weight:900;font-size:1.15rem;margin-top:.6rem;padding-top:.6rem;border-top:1px dashed var(--border);}

/* pay + CTA */
.pay-grid{display:grid;grid-template-columns:1fr 1fr;gap:.6rem;margin:.9rem 0 1rem;}
.pay-opt{display:flex;align-items:center;gap:.5rem;padding:.75rem;border:1px solid var(--border);border-radius:.7rem;background:#fff;cursor:pointer;transition:border-color .15s,background .15s;}
.pay-opt:hover{background:#f9fafb;}
.place-order{width:100%;height:48px;border:none;border-radius:.8rem;font-weight:800;color:#fff;background:linear-gradient(90deg,var(--brand-1),var(--brand-2));cursor:pointer;transition:.12s;}
.place-order:hover{transform:translateY(-1px);filter:brightness(1.03);}
.place-order:disabled{cursor:not-allowed;opacity:.55;background:#e5e7eb;color:#6b7280;}

@media(min-width:1024px){.summary{position:sticky;top:88px;}}
.cart-empty{text-align:center;padding:4rem 1rem;}
.cart-empty h3{margin:.25rem 0 .5rem;font-weight:800;}